.hubspot {
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.85);
    margin: 2%;
    padding: 10%;
    display: block;
}
.box {
    padding: 2%;
}

table {
    text-align: center;
    width: 100%;
    display: block;
    overflow-x: auto;
}

table td, table th {
    padding: 8px;
    border: 1px solid rgb(255, 255, 255);
}

table th {
    text-align: center;
    color: rgb(255, 255, 255);
}

tbody tr:hover {
    background-color: rgba(255, 255, 255, 0.10);
}



body {
    background: url(/static/media/portfolio-bg.d69ac298.jpg);
    font-size: 18px;
}

a {
    color: rgb(255, 255, 255) !important;
}

.box input {
    text-align: center;
    background-color: rgba(0, 0, 0, 0.50);
    color: rgb(255, 255, 255);
    width: 60%;
    margin: auto;
}

.success-text {
    color: rgb(0, 128, 0);
}

.warning-text {
    color: rgb(255, 200, 0);
}

.error-text {
    color: rgb(255, 0, 0);
}

.warning-btn {
	background-color: rgba(255, 165, 0, 0.35);
	border-radius: 15px;
}

.error-btn {
    background-color: rgba(255, 0, 0, 0.35);
	border-radius: 15px;  
}

.rounded-img {
	border-radius: 8px;
}

.left-link {
    float: left;
    padding-left: 8%;
}

.right-link {
    float: right;
	padding-right: 8%;
}

.box {
    background-color: rgba(0, 0, 0, 0.75);
    box-shadow: 7px 8px 8px rgba(0, 0, 0, 0.85);
    border-radius: 6px;
    margin-top: 1%;
    margin-bottom: 1%;
    text-align: center;
    color: rgb(255, 255, 255);
    width: 85%;
}

.middle-box {
    width: 60%;
    padding: 1%;
}

.small-box {
    width: 40%;
}

@media only screen and (max-width: 600px) {
    .box {
        margin-top: 5%;
    }
    .box input {
        width: 90%;
    }
	#blog-img, #snake-img {
        width: 100%;
    }
    .middle-box, .small-box {
        width: 85%;
    }
    body {
        font-size: 16px;
    }
}
